<template>
	<view class="container">
		<view class="quikmenu">
			<view class="item">
				<view class="label">形象照</view>
				<view class="thumb" @click="checkImg('thumb')" v-if="formData.thumb==''">
					<uni-icons type="plus" size="24" color="#f2f2f2"></uni-icons>
				</view>
				<image :src="formData.thumb" class="thumb" mode="scaleToFill" v-else></image>
			</view>
			<view class="item">
				<view class="label">身份证（正面）</view>
				<view class="thumb" @click="checkImg('idcard1')" v-if="formData.idcard1==''">
					<uni-icons type="plus" size="24" color="#f2f2f2"></uni-icons>
				</view>
				<image :src="formData.idcard1" class="thumb" mode="scaleToFill" v-else></image>
			</view>
			<view class="item">
				<view class="label">身份证（反面）</view>
				<view class="thumb" @click="checkImg('idcard2')" v-if="formData.idcard2==''">
					<uni-icons type="plus" size="24" color="#f2f2f2"></uni-icons>
				</view>
				<image :src="formData.idcard2" class="thumb" mode="scaleToFill" v-else></image>
			</view>
			<view class="item">
				<view class="label">姓名</view>
				<uni-easyinput type="text" v-model="formData.name" placeholder="姓名" class="input" />
			</view>
			<view class="item">
				<view class="label">电话</view>
				<uni-easyinput type="text" v-model="formData.tel" placeholder="电话" class="input" />
			</view>
			<view class="item" @click="openLocation">
				<view class="label">地区</view>
				<uni-easyinput type="text" v-model="formData.address_name" suffixIcon="arrowdown" placeholder="点击选择地址"
					class="input" disabled />
			</view>
			<view class="item">
				<view class="label">详细地址</view>
				<uni-easyinput type="text" v-model="formData.address" placeholder="请输入详细地址" class="input" />
			</view>
			<view class="item">
				<view class="label">选择服务</view>
				<checkbox-group @change="checkboxChange">
					<view class="checklists">
						<block v-for="(item,index) in cates" :key="index">
							<view class="title">
								<checkbox color="#FF4400" :value="item.id" style="transform:scale(0.7)" />
								{{item.title}}
							</view>
						</block>
					</view>
				</checkbox-group>
			</view>
			<view class="add" @click="save">入住</view>
		</view>
	</view>
</template>

<script>
	const app = getApp()
	//const key = 'MRCBZ-P6NW3-I4P3B-3GYN4-XCESS-MMFVP'; //使用在腾讯位置服务申请的key
	const referer = 'app'; //调用插件的app的名称

	const chooseLocation = requirePlugin('chooseLocation');

	export default {
		data() {
			return {
				formData: {
					uid: 0,
					name: '',
					tel: '',
					address_name: '',
					province:'',
					city:'',
					district:'',
					address: '',
					latitude: '',
					longitude: '',
					isdefault: false,
					cates: [],
					thumb: '',
					idcard1: '',
					idcard2: ''
				},
				isdefault: false,
				cates: [],
				detail: {}
			}
		},
		onLoad() {
			this.getCates()
			this.getDetail()
		},
		onShow() {
			if (this.vuex_login == 0) {
				this.$u.route('pages/login/index');
				return
			}
			this.formData.uid = this.vuex_user.id
			const location = chooseLocation.getLocation(); // 如果点击确认选点按钮，则返回选点结果对象，否则返回null
			console.log(location)
			if (location) {
				this.formData.address_name = location.province + ' ' + location.city + ' ' + location.district
				this.formData.province = location.province
				this.formData.city = location.city
				this.formData.district = location.district
				this.formData.address = location.address
				this.formData.latitude = location.latitude
				this.formData.longitude = location.longitude
			}

		},
		onUnload() {
			// 页面卸载时设置插件选点数据为null，防止再次进入页面，geLocation返回的是上次选点结果
			chooseLocation.setLocation(null);
		},
		methods: {
			openLocation() {
				wx.navigateTo({
					url: 'plugin://chooseLocation/index?key=' + app.globalData.map + '&referer=' + referer +
						'&location=&category='
				});
				// uni.chooseLocation({
				// 	success: (res) => {
				// 		console.log('位置名称：' + res);
				// 		console.log('详细地址：' + res.address);
				// 		console.log('纬度：' + res.latitude);
				// 		console.log('经度：' + res.longitude);
				// 		this.formData.address_name = res.name
				// 		this.formData.address =  res.address
				// 		this.formData.latitude = res.latitude
				// 		this.formData.longitude = res.longitude
				// 	}
				// });
			},
			async getDetail() {
				let res = await this.$u.get('/v2/shop/teacher/view', {
					uid: this.vuex_user.id
				})
				this.detail = res.data
				this.formData = {
					uid: this.detail.uid,
					name: this.detail.name,
					tel: this.detail.phone,
					address_name:  this.detail.province + ' ' + this.detail.city + ' ' + this.detail.district,
					province:this.detail.province,
					city:this.detail.city,
					district:this.detail.district,
					address: this.detail.address,
					latitude: '',
					longitude: '',
					isdefault: false,
					cates: [],
					thumb: this.detail.thumb,
					idcard1: this.detail.idcard1,
					idcard2: this.detail.idcard2
				}
			},
			async getCates() {
				let res = await this.$u.get('/v2/category/index')
				this.cates = res.data.data
			},
			async save() {
				this.formData.uid = this.vuex_user.id
				let bool = Object.values(this.formData).every((item) => item !== "");
				if (!bool) {
					this.$u.toast('有选项没填')
					return
				}
				if (!this.$u.test.mobile(this.formData.tel)) {
					this.$u.toast('手机格式不正确')
					return
				}
				let res = await this.$u.post('/v2/shop/register/save', this.formData)
				if (res.data.code == 0) {
					this.$u.toast(res.message)
					return
				}
				uni.navigateBack({
					delta: 1
				})

			},
			checkboxChange(e) {
				console.log(e.detail.value)
				this.formData.cates = e.detail.value
			},
			checkImg(type) {
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					success: (res) => {
						const tempFilePaths = res.tempFilePaths;
						tempFilePaths.forEach((item) => {
							this.upload(item, type)
						})
					}
				});
			},
			upload(file, type) {
				uni.uploadFile({
					url: this.$baseUrl() + '/v2/file/images',
					filePath: file,
					name: 'file',
					success: (res) => {
						let data = JSON.parse(res.data)
						this.formData[type] = data.data.url
					},
					complete: (res) => {
						console.log(res);
					}
				});
			},
		}
	}
</script>

<style scoped>
	.container {
		min-height: 100vh;
		background-color: #f2f2f2;
		padding-top: 1px;
		display: flex;
		flex-direction: column;

	}

	.quikmenu {
		display: flex;
		flex-direction: column;
		background-color: #fff;
		padding: 20rpx;
	}

	.quikmenu .item {
		display: flex;
		flex-direction: column;
		font-size: 28rpx;
		margin: 10rpx 0;
	}

	.quikmenu .item .input {
		flex: 1;
	}

	.quikmenu .item .label {
		margin: 0 30rpx 20rpx 0;
		font-size: 28rpx;
	}

	.add {
		margin: 0 0;
		background-color: #007AFF;
		height: 90rpx;
		border-radius: 16rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;
	}

	.checklists {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-row-gap: 10rpx;
		grid-column-gap: 20rpx;
	}

	.checklists .title {
		border-radius: 8rpx;
		font-size: 24rpx;
		margin: 10rpx 0;
		padding: 10rpx 0;
		background-color: #f2f2f2;
		color: #333
	}

	.thumb {
		width: 710rpx;
		height: 320rpx;
		border-radius: 8rpx;
		border: #f2f2f2 1px dashed;
		display: flex;
		justify-content: center;
		align-items: center;
		box-sizing: border-box;
	}
</style>
